@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mitr&family=Sriracha&display=swap");
.NavElement {
  display: inline-block;
  text-decoration: none;
  color: black;
}

.NavElement:hover {
  color: #3990af;
  transition: color 0.3s ease-out;
}
.CollectionsElement {
  margin: 5px;
  border: solid 1px #b6e4f5;
}

.CollectionsElement:hover {
  border: solid 1px #0586b4;
}

.Txt {
  font-family: "Roboto";
  margin-bottom: 10px;
  font-size: larger;
}

.Image {
  max-height: 300px;
  width: 300px;
}

.Hide {
  display: none;
}
